<template>
	<div class="policyadd">
		<el-breadcrumb separator-class="el-icon-arrow-right">
			<el-breadcrumb-item>销售管理</el-breadcrumb-item>
			<el-breadcrumb-item class="attrbute">
				<span @click="goBack">促销政策</span>
			</el-breadcrumb-item>
			<el-breadcrumb-item>{{title}}促销政策</el-breadcrumb-item>
		</el-breadcrumb>
		<div class="contbk">
			<h2 class="cont_tit">{{title}}促销政策</h2>
			<el-form :inline="true" :model="params" class="cont_list" ref="policyRef">
				<el-form-item label="销售组织:" size="mini" prop="company_id" :rules="rules.company_id">
					<el-select v-model="params.company_id" placeholder="请选择" filterable @focus="companyListInvok" @change="companyChange">
						<el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
							<span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
						</el-option>
					</el-select>
				</el-form-item>
				<br>
				<el-form-item size="mini" prop="policy_name" label="政策名称:" :rules="rules.policy_name">
					<el-input v-model="params.policy_name" maxlength="100" placeholder="请输入"></el-input>
				</el-form-item>
				<br>
				<el-form-item size="mini" prop="cxzctime" :rules="rules.cxzctime" label="政策生效日期:">
					<el-date-picker v-model="params.cxzctime" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期" @change="timeChange">
					</el-date-picker>
					<span class="smstyle">说明：促销政策有效期在业务日期内，促销政策有效</span>
				</el-form-item>
				<br>
				<el-row style="margin:20px 0;">
					<span class="resetH2">促销商品:</span>
					<el-table :highlight-current-row="true" :row-class-name="tableRowClassName" tooltip-effect="light" border :data="goodslist" max-height="298" class="goodslist" @selection-change="handleSelectionChange" v-loading="loading1">
						<el-table-column type="index" label="序号" width="45"></el-table-column>
						<el-table-column label="操作" width="60">
							<template slot-scope="scope">
								<i class="el-icon-circle-plus" @click="addGoodlist(scope.$index, scope.row)"></i>&nbsp;&nbsp;&nbsp;
								<i class="el-icon-error" @click="delGoodlist(scope.$index, scope.row)"></i>
							</template>
						</el-table-column>
						<el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号" width="70">
						</el-table-column>
						<el-table-column property="goods_name" label="商品名称" min-width="290">
							<template slot-scope="scope">
								<div style="width:95%;float:left;">
									<vGoodsList :scopes="scope" :showObj="showObj" :vxeParams="params" :changeGoods="changeGoods1" />
								</div>
								<div style="float:right;width:5%;margin-top:5px;">
									<i @click="openIndexgoods(scope.$index)" class="el-icon-search"></i>
								</div>
							</template>
						</el-table-column>
						<el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌" min-width="110">
						</el-table-column>
						<el-table-column :show-overflow-tooltip="true" property="unit_name" min-width="40" label="单位">
						</el-table-column>
					</el-table>
					<div style="float:left;margin-left:106px;" class="smstyle">
						【说明】 1、点击商品名称中的放大镜图标，可批量选择商品；2、将对以上每一个商品分别应用促销规则。
					</div>
				</el-row>

				<el-row>
					<span class="resetH">促销类型:</span>
					<el-radio-group v-model="params.unit_type" @change="getlable">
						<el-radio :label="3">按照商品数量</el-radio>
						<el-radio :label="4">按照商品金额</el-radio>
						<el-radio :label="5">特价</el-radio>
					</el-radio-group>
				</el-row>

				<el-row style="margin:20px 0;">
					<span class="resetH2">促销规则:</span>
					<el-radio-group v-model="params.full_type" v-if="params.unit_type!=5" @change="getlable">
						<el-radio :label="2">满减</el-radio>
						<el-radio :label="3">满折</el-radio>
						<el-radio :label="1">满赠</el-radio>
					</el-radio-group>
					<div class="flagstyle pdbj" v-if="params.unit_type!=5">
						{{textone}}
						<el-input @change.native="handleInput2" v-model="params.full_price"></el-input> {{texttwo}}
						<el-input @input.native="handinput1" @change.native="handleInput2" v-model="params.full_subtraction_price"></el-input> {{textthree}}
						<span class="btncustomer" v-if="params.full_type==1" @click="isShowGoodsLayer = true">添加赠品</span>
					</div>
					<div style="float:left;margin:10px 0 0 106px;" v-if="params.unit_type!=5 && params.full_type!=3">
						<el-checkbox v-model="params.is_double" false-label="0" true-label="1">启动倍送</el-checkbox>
						<span class="smstyle">（启用后，按照本次条件的整数倍进行优惠；例：启用时，满3减/赠1，如果满7则减/赠2）</span>
					</div>
					<div v-if="params.unit_type==5" class="flagstyle">
						商品售价降至
						<el-input @change.native="handleInput2" v-model="params.full_price"></el-input> 元
					</div>
					<div class="customBk" v-if="params.full_type==1&&params.unit_type!=5">
						<el-tag v-for="tag in goods" @close="handleClosegoods(tag)" :key="tag.goods_id" closable :type="tag.type">
							{{tag.goods_name}}
						</el-tag>
					</div>
				</el-row>

				<el-row style="margin-bottom:20px">
					<span class="resetH2">促销范围:</span>
					<el-radio-group v-model="params.promotion_range" @change="getlablecustomer">
						<el-radio :label="2">全部客户</el-radio>
						<el-radio :label="1">指定客户</el-radio>
					</el-radio-group>
					<span class="btncustomer" v-if="params.promotion_range==1" @click="isShowCustomLayer = true">选择客户</span>
					<div class="customBk" v-if="params.promotion_range==1">
						<el-tag v-for="(tag,index) in customs" @close="handleClose(tag)" :key="tag.customer_id" closable :type="tag.type">
							{{index+1}}、&nbsp;{{tag.customer_id}}&nbsp;{{tag.customer_name}}&nbsp;{{tag.customer_mobile}}
						</el-tag>
					</div>
				</el-row>

				<el-form-item size="mini" label="备注:">
					<el-input v-model="params.remark" maxlength="300" placeholder="请输入" suffix-icon="el-icon-edit-outline"></el-input>
				</el-form-item>
				<div style="float:left;margin-left:106px;" class="smstyle">
					【说明】 在相同生效时间内，相同的商品、客户，以最新的促销政策为准。
				</div>
			</el-form>
			<!-- 确定按钮部分 -->
			<el-row type="flex" justify="center" style="width:780px;margin:30px 0 ;">
				<el-col :span="4">
					<div class="grid-content bg-purple-light">
						<!-- :disabled="flagbtn" -->
						<el-button type="primary" @click="submit('policyRef')" size="mini">确定</el-button>
					</div>
				</el-col>
				<el-col :span="4">
					<div class="grid-content bg-purple">
						<el-button plain @click="goBack" size="mini">取消</el-button>
					</div>
				</el-col>
			</el-row>
		</div>

		<!-- 客户列表 -->
		<selectCustomLayer v-if="isShowCustomLayer" @selectCustomResult="selectCustomResult" :companyId="params.company_id" :choose_status="params.promotion_range" :choose_customer_str="customs" />
		<!-- 赠品弹框 -->
		<selectGoods v-if="isShowGoodsLayer" @goodsdialog='goodsdialog' :companyId="params.company_id" />
		<!-- 商品弹框 -->
		<goodsPopout v-if="isShowGoodsLayer2" @close='isShowGoodsLayer2 = false' :incloudTop="['pattern','brand_name','specification_type','unit_name']" url='getGoodsListDropdown' :tableColumnConfig="[{old_field: 'sale_price',field: 'unit_price'}]" :ExcloudParams="['warehouse',,'is_stocks','is_available_stocks']" :searchParams="{company_id :params.company_id}" width='1100' @submit="goodsdialog2" rowId='goods_id' />

	</div>
</template>

<script>
	import { mapActions } from "vuex";
	import selectGoods from "@/components/element/sales/share/selectGoods.vue";
	import selectGoods2 from "@/components/element/sales/share/selectGoods2.vue";
	import selectCustomLayer from "@/components/element/sales/share/selectCustomLayer.vue";
	import vGoodsList from "common/components/plugin/vGoodsList.vue";
	import {
		getCompanyListCommon,
		bindIdsF,
		opthionList
	} from "common/utils/public_function/commonUtils";

	export default {
		name: "salesPolicyadd",
		data() {
			return {
				flagbtn: false,
				showObj: {
					goods_id: "商品编号",
					goods_name: "商品名称"
				},
				listchuan: {},
				goodslist: [],
				opengoodsindex: 0,
				textone: "商品数量满",
				texttwo: "则减免",
				textthree: "元",
				title: "新增",
				isShowCustomLayer: false, //客户弹框显示条件
				isShowGoodsLayer: false, //商品弹框显示条件
				isShowGoodsLayer2: false, //商品弹框显示条件2
				customs: [], // 客户列表的动态展示
				goods: [], //商品列表展示
				loading: true,
				brandList: [], //品牌管理相关
				list: [], //品牌管理相关
				// 添加接口需要传递的参数
				params: {
					promotion_range: 2,
					policy_name: null, //促销名称
					customer_type_id: null, //客户分类ID
					company_id: null, //销售组织ID
					start_time: null, //开始时间
					end_time: null, //结束时间
					brand_id: null, //品牌ID
					remark: null, //备注
					customer_user_id_str: [], //客户列表id串，逗号隔开 需要处理下都是弹框返回的
					goods_id_str: [], //商品列表id串  需要处理下都是弹框返回的
					gift_goods_id_str: [], //满赠商品id串 需要处理下都是弹框返回的
					unit_type: null, //是否单价优惠类型（0否 1优惠价格 2优惠价格比例）默认不传为0
					unit_value: null, //单件优惠值（根据设置单件优惠，可以是优惠价格也可以是优惠比例）
					full_type: null, //是否满减或满赠（0否1满赠2满减）默认不传为0
					full_price: null, //满足条件的价格值（如果选择满减或者满增）u
					full_subtraction_price: null //满减的价格（如果选择满减，必填项）
				}
			};
		},
		components: {
			selectCustomLayer,
			selectGoods,
			selectGoods2,
			vGoodsList
		},
		methods: {
			...mapActions([
				"paramsLookuser_hide",
				"promotionpolicyIndexACT",
				"paramsAdd_hide"
			]),
			handinput1(e) {
				e.target.value = e.target.value.match(/^\d*(\.?\d{0,2})/g)[0] || "";
			},
			handleInput2(e) {
				if (e.target.value <= 0) {
					// e.target.value = e.target.value.match(/^\d*(\.?\d{0,2})/g)[0] || null;
					e.target.value = "";
				}
				this.$forceUpdate();
			},
			tableRowClassName({ row, rowIndex }) {
				if (row.canNotDelete) {
					return "error-row";
				}
				return "";
			},
			openIndexgoods(index) {
				this.opengoodsindex = index;
				this.isShowGoodsLayer2 = true;
			},
			changeGoods1(value, scope) {
				let goodsInfo = [
					"goods_id",
					"goods_sn",
					"goods_name",
					"unit_name",
					"unit_id",
					"brand_name"
				];
				goodsInfo.forEach((item, i, array) => {
					this.$set(this.goodslist[scope.$index], item, value[item]);
				});
			},
			getlablecustomer() {
				this.$set(this, "customs", []);
				this.$set(this.params, "customer_str", []);
			},
			getlable(val) {
				if (val) {
					this.$set(this.params, "full_price", "");
					this.$set(this.params, "full_subtraction_price", "");
				}

				if (this.params.unit_type == 3) {
					// 按照商品数量
					switch (this.params.full_type) {
						case 2:
							this.textone = "商品数量满";
							this.texttwo = "则减免";
							this.textthree = "元";
							break;
						case 3:
							this.textone = "商品数量满";
							this.texttwo = "则打";
							this.textthree = "折";
							break;
						case 1:
							this.textone = "商品数量满";
							this.texttwo = "则赠送数量";
							this.textthree = "的";
							break;
					}
				} else if (this.params.unit_type == 4) {
					// 按照商品金额
					switch (this.params.full_type) {
						case 2:
							this.textone = "商品金额满";
							this.texttwo = "则减免";
							this.textthree = "元";
							break;
						case 3:
							this.textone = "商品金额满";
							this.texttwo = "则打";
							this.textthree = "折";
							break;
						case 1:
							this.textone = "商品金额满";
							this.texttwo = "则赠送数量";
							this.textthree = "的";
							break;
					}
				} else {
					//全都不展示
				}
			},
			addGoodlist(index, row) {
				let newRows = Object.assign({}, {});
				let arr = this.goodslist;
				arr.splice(index + 1, 0, newRows);
				this.$set(this, "goodslist", arr);
			},
			delGoodlist(index, row) {
				let arr = this.goodslist;
				arr.splice(index, 1);
				this.$set(this, "goodslist", arr);
				if (this.goodslist.length == 0) {
					this.goodslist.push({
						goods_id: "",
						goods_name: "",
						brand_name: "",
						unit_name: ""
					});
				}
			},
			companyChange() {
				this.$set(this, "goodslist", []);
				for (let index = 0; index < 8; index++) {
					this.goodslist.push({
						goods_id: "",
						goods_name: "",
						brand_name: "",
						unit_name: ""
					});
				}
				this.$set(this, "goods", []);
				this.$set(this, "customs", []);
			},
			timeChange() {
				if (this.params.cxzctime) {
					this.params.start_time = this.params.cxzctime[0];
					this.params.end_time = this.params.cxzctime[1];
				} else {
					this.params.start_time = "";
					this.params.end_time = "";
				}
			},
			customerTypeFocus() {
				this.$common.options("getCustomerTypeListOption", this.customerTypeList, {
					company_id: this.params.company_id,
					is_page: 1
				});
			},
			goodsdialog(val) {
				// 赠品
				this.isShowGoodsLayer = false;
				let goodsIdArr = this.goods.map(item => {
					return item.goods_id;
				});
				val.forEach(item => {
					if (!goodsIdArr.includes(item.goods_id)) {
						this.goods.push(item);
					}
				});
				if (this.goods || this.goods.length != 0) {
					this.params.gift_str = this.goods;
				}
			},
			goodsdialog2(val) {
				// 接收满减商品的列表返回值
				this.isShowGoodsLayer2 = false;
				this.goodslist.splice(this.opengoodsindex, 0, ...val);
			},
			goBack() {
				// 取消跳转到列表页面
				this.$publicFution.goPage(this.$route.query.id);
				this.$router.push({ path: "/sales/promotionpolicy" });
			},
			submit(ruleForm) {
				this.$refs[ruleForm].validate(valid => {
					if (valid) {
						// this.flagbtn = true;
						let params = Object.assign({}, this.params);
						// goods_str  促销商品
						// customer_str 促销客户
						// gift_str  赠品
						// params.start_time = this.params.cxzctime[0];
						// params.end_time = this.params.cxzctime[1];
						if (this.params.customer_str) {
							params.customer_str = this.params.customer_str
								.map(item => item.customer_id)
								.join(",");
						}
						if (this.goodslist) {
							let a = this.goodslist;
							params.goods_str = a.filter(item => item.goods_name != "");
							params.goods_str = params.goods_str
								.map(item => item.goods_id)
								.join(",");
						}
						if (this.params.gift_str) {
							params.gift_str = this.params.gift_str
								.map(item => item.goods_id)
								.join(","); //赠品ID
						}
						let api = this.$route.query.id ? "editCXmessage" : "addCXmessage";
						this.$axMethod[api](params).then(res => {
							if (res.data.code == 200) {
								if (res.data.msg == "重复请求") {
									this.$message({
										message: res.data.msg,
										type: "error"
									});
								} else {
									this.$message({
										message: res.data.msg,
										type: "success"
									});
									setTimeout(() => {
										this.goBack();
									}, 1000);
								}
							} else {
								let error_customer = res.data.data.error_customer
									? res.data.data.error_customer
									: (res.data.data.error_customer = []);
								let error_goods = res.data.data.error_goods
									? res.data.data.error_goods
									: (res.data.data.error_goods = []);
								this.goodslist.forEach(item => {
									if (error_goods.indexOf(item.goods_id) != -1) {
										item.canNotDelete = true;
									}
								});
								this.goodslist.push({});
								this.goodslist.pop();
								this.customs.forEach(item => {
									if (error_customer.indexOf(item.customer_id) != -1) {
										item.type = "danger";
									}
								});
								this.customs.push({});
								this.customs.pop();
								// this.flagbtn = false;
							}
						});
					} else {
						// this.flagbtn = false;
						console.error("error submit!!");
						return false;
					}
				});
			},
			selectCustomResult(val) {
				// 接受客户的列表返回值
				let customerIdArr = this.customs.map(item => {
					return item.customer_id;
				});
				val.forEach(item => {
					if (!customerIdArr.includes(item.customer_id)) {
						this.customs.push(item);
					}
				});
				if (this.customs && this.customs.length) {
					this.params.customer_str = this.customs;
				}
			},
			handleClosegoods(tag) {
				//    移除满赠标签商品
				this.goods.splice(this.goods.indexOf(tag), 1);
				this.params.gift_str = this.goods;
			},
			handleClose(tag) {
				//    移除标签客户
				this.customs.splice(this.customs.indexOf(tag), 1);
				this.params.customer_str = this.customs;
			}
		},

		mounted() {
			if (this.$route.query && this.$route.query.id) {
				this.title = "修改";
				this.params.type = 2;
				this.$axios({
					method: "get",
					url: this.$api.getPromotionPolicyInfo,
					params: {
						policy_id: this.$route.query.id
					}
				}).then(res => {
					opthionList(this, res.data.data);
					this.params = res.data.data;
					this.customs = this.params.customer_list; //客户列表小标签
					this.goodslist = this.params.goods_list; //商品列表小标签
					this.goods = this.params.gift_goods_list; //满赠商品列表小标签
					// 时间转换
					this.params.customer_str = this.customs;
					this.params.gift_str = this.goods;
					this.params.goods_str = this.goodslist;
					this.params.type = 2;
					this.$set(this.params, "cxzctime", [
						this.params.start_time,
						this.params.end_time
					]);
					this.getlable();
				});
			} else {
				for (let index = 0; index < 8; index++) {
					this.goodslist.push({
						goods_id: "",
						goods_name: "",
						brand_name: "",
						unit_name: ""
					});
				}
				this.$set(this, "params", {
					company_id: Number(localStorage.company_id),
					type: 2,
					promotion_range: 2,
					unit_type: 3, //促销类型
					full_type: 2 //促销规则
				});
				bindIdsF(this, window.localStorage, [
					["company_id", "company_name", "companyList"]
				]);
			}
		}
	};
</script>

<style scoped  lang="less">
	.policyadd {
		min-width: 953px;
	}
	.smstyle {
		color: #e4723f;
		font-size: 13px;
	}
