<template>
  <!-- 选择客户 弹框 -->
  <div>
    <!-- :visible.sync="isShowCustomLayer" -->
    <el-dialog :close-on-click-modal="false" title="选择客户" :visible="true" width="962px" @close="cancel()">
      <el-form :inline="true" ref="params" :model="params">
        <el-form-item class="dQzcCustom" label="客户名称" size="mini" label-width="95px" style="width:256px;">
          <el-select v-model="params.customer_id" filterable placeholder="请输入" clearable @focus="clienteleListInvok" :loading="loading" remote :remote-method="clienteleListInvok">
            <ul class="customer customer-ul4 customer-ul2">
              <li>客户编号</li>
              <li>客户名称</li>
              <li>联系人</li>
              <li>联系电话</li>
              <li>区域</li>
            </ul>
            <el-option v-for="item in customerList" :key="item.customer_id" :label="item.customer_name" :value="item.customer_id">
              <ul class="customer customer-ul4">
                <li :title="item.customer_id">{{ item.customer_id }}</li>
                <li :title="item.customer_name">{{ item.customer_name }}</li>
                <li :title="item.contact_name">{{item.contact_name}}</li>
                <li :title="item.customer_mobile">{{item.customer_mobile}}</li>
                <li>{{item.province_name}}/{{item.city_name}}/{{item.area_name}}</li>
              </ul>
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item class="dQzcCustom" label="手机号码" size="mini" label-width="95px" style="width:256px;">
          <el-input v-model="params.customer_mobile"></el-input>
        </el-form-item>
        <el-form-item label="客户分类" size="mini">
          <el-select clearable v-model="params.customer_type_id" @focus="customerTypeInvok" @change="clearCustomer">
            <el-option :label="item.customer_type_name" :value="item.customer_type_id" :key="item.customer_type_id" v-for="item in customerTypeList"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="选择状态" size="mini" class="dQzcCustom" label-width="95px" style="width:256px;">
          <el-select v-model="params.choose_status" clearable>
            <el-option label="全部" :value="0">全部</el-option>
            <el-option label="已选择" :value="2">已选择</el-option>
            <el-option label="未选择" :value="1">未选择</el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="区域" class="el-form-item_shortInput " clearable size="mini">
          <el-cascader :options="options" @change="changes" @expand-change="handleItemChange" :props="props1" v-model="clearuser"></el-cascader>
        </el-form-item>

        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>

      <div class="table_container">
        <!-- 下边是表格 -->
        <el-table :highlight-current-row="true" tooltip-effect="light" border ref="multipleTable" max-height="300" :data="TableDataCustom.tableData" style="width: 100%" @selection-change="handleSelectionChange">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" label="选择">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_id" label="客户编号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_name" label="客户名称">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_mobile" label="手机号码">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="customer_type_name" label="客户分类">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="addressDetail" label="所属区域">
          </el-table-column>
        </el-table>
        <div class="page_container">
          <el-pagination class="erp-pagination" @current-change="handleCurrentChange" @size-change="handleSizeChange" :current-page.sync="customListPage" layout="total,sizes, prev, pager, next,jumper" :page-sizes="per_page" :page-size="pageSize1" prevText="上一页" nextText="下一页" :total="TableDataCustom.total">
          </el-pagination>
        </div>

      </div>

      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import { getCustomerList } from "common/utils/public_function/commonUtils";
export default {
  name: "selectCustomLayer",
  data() {
    return {
      params: {}, //弹框表单字段
      options: [],
      selectedOptions: [, ,], //省市级val
      customListPage: 1, //当前页
      TableDataCustom: {
        tableData: [],
        total: 10
      },
      gather: [],
      radio: "",
      loading:false,
      props1: {
        value: "value",
        children: "cities"
      },
      clearuser: [],
      pagechoose_customer_str: ""
    };
  },
  // choose_customer_str客户的串
  props: [
    "isShowCustomLayer",
    "companyId",
    "choose_status",
    "choose_customer_str"
  ],
  methods: {
    clearCustomer(val) {
      this.$set(this.params, "customer_id", "");
    },
    getCustomerListRemote(query) {
      getCustomerList(this, {
        company_id: this.companyId,
        choose_customer_str: this.pagechoose_customer_str,
        choose_status: this.params.choose_status,
        customer_name: query
      });
    },
    getCustomList(params) {
      this.$common.initData(
        "getCustomerList",
        this.TableDataCustom,
        Object.assign({}, params, {
          company_id: this.companyId,
          choose_customer_str: this.pagechoose_customer_str,
          choose_status: this.params.choose_status,
          page: this.customListPage,
          per_page: 30
        })
      );
    },
    handleSizeChange(val) {
      this.$common.initData(
        "getCustomerList",
        this.TableDataCustom,
        Object.assign({}, params, {
          per_page: val,
          page: 1
        })
      );
    },
    cancel() {
      this.$parent.isShowCustomLayer = false;
    },
    submit() {
      this.$emit("selectCustomResult", this.gather);
      this.$parent.isShowCustomLayer = false;
    },
    onSearch() {
      //查询
      this.getCustomList(
        Object.assign({}, this.params, {
          province_id: this.selectedOptions[0],
          city_id: this.selectedOptions[1],
          area_id: this.selectedOptions[2]
        })
      );
    },
    onReset() {
      //重置
      this.customListPage = 1;
      this.selectedOptions = [];
      this.$set(this, "params", {});
      this.$set(this, "clearuser", []);
      let params = {};
      this.$common.initData(
        "getCustomerList",
        this.TableDataCustom,
        Object.assign({}, params, {
          page: this.customListPage,
          company_id: this.companyId,
          customer_type_id: this.customertypeId,
          per_page: 30
        })
      );
    },
    handleSelectionChange(val) {
      this.gather = val;
    },
    handleCurrentChange(val) {
      this.customListPage = val;
      this.getCustomList();
    },
    changes(val) {
      this.selectedOptions = val;
    },
    handleItemChange(val) {
      var p_id;
      if (val.length == 1) {
        p_id = val[0];
      }
      if (val.length == 2) {
        p_id = val[1];
      }
      this.$axios({
        method: "get",
        url: this.$api.getAreaListOption,
        params: {
          p_id: p_id,
          is_page: 1
        }
      }).then(res => {
        for (var i in this.options) {
          if (
            val.length == 1 &&
            this.options[i].value == val[0] &&
            !this.options[i].cities.length
          ) {
            for (var j in res.data.data) {
              var cities = {};
              cities.label = res.data.data[j].area_name;
              cities.value = res.data.data[j].area_id;
              cities.cities = [];

              this.options[i].cities.push(cities);
            }
          }

          if (val.length == 2) {
            for (var k in this.options[i].cities) {
              if (
                this.options[i].cities[k].value == val[1] &&
                !this.options[i].cities[k].cities.length
              ) {
                for (var j in res.data.data) {
                  var cities = {};
                  cities.label = res.data.data[j].area_name;
                  cities.value = res.data.data[j].area_id;
                  this.options[i].cities[k].cities.push(cities);
                }
              }
            }
          }
        }
      });
    },
    reDataCustom() {
      // 整理返回TableDataCustom 数据
      for (let item of this.TableDataCustom.tableData) {
        Object.assign(item, {
          addressDetail: `
          ${item.province_name}${item.city_name}${item.area_name}${item.address}
        `
        });
      }
    }
  },
  mounted() {
    if (this.choose_status == 1) {
      if (this.choose_customer_str.length >= 1) {
        this.pagechoose_customer_str = this.choose_customer_str
          .map(item => item.customer_id)
          .join(",");
        this.$set(this.params, "choose_status", 1);
      } else {
        this.$set(this.params, "choose_status", 1);
      }
    }
    this.params.company_id = this.companyId;
    this.getCustomList();
    this.$axios({
      method: "get",
      url: this.$api.getAreaListOption,
      params: {
        level: 2,
        is_page: 1
      }
    }).then(res => {
      var province = {};
      for (var i in res.data.data) {
        var province = {};
        province.label = res.data.data[i].area_name;
        province.value = res.data.data[i].area_id;
        province.cities = [];
        this.options.push(province);
      }
    });
  },

  watch: {
    TableDataCustom: {
      handler: function() {
        this.reDataCustom();
      },
      deep: true
    }
  }
};
</script>

<style lang="less">
// 单前政策
.dQzcCustom .el-form-item__content {
  width: 150px;

  .el-input {
    width: 150px;
  }
}
</style>
