<template>
  <div>
    <el-dialog :close-on-click-modal="false" title="选择商品" :visible="true" width="1030px" @close="cancel()">
      <div class="search_container">
        <el-form :inline="true" :model="params" ref="ruleForm" class="demo-form-inline">
          <el-form-item label="商品类型" label-width="65px">
            <jybd-cascader @cascaderMethod="cascaderMethod" ref="cascader"></jybd-cascader>
          </el-form-item>
          <br>
          <el-form-item label="商品名称" size="mini" label-width="65px">
            <el-input v-model="params.goods_name"></el-input>
          </el-form-item>
          <el-form-item label="商品编号" size="mini" label-width="65px">
            <el-input v-model="params.goods_id"></el-input>
          </el-form-item>
          <el-form-item label="品牌" size="mini" label-width="50px">
            <getBrand v-model='params.brand_id' />
          </el-form-item>
          <el-form-item label="规格型号" size="mini" label-width="65px">
            <el-input v-model="params.specification_name"></el-input>
          </el-form-item>
          <el-form-item label="花纹" size="mini" label-width="65px">
            <el-input v-model="params.figure_name"></el-input>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
        <el-table :highlight-current-row="true" tooltip-effect="light" border ref="multipleTable" :data="TableData.tableData" style="width: 100%" @selection-change="handleSelectionChange" max-height="300">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" :reserve-selection="false">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_id" label="商品编号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="goods_name" label="商品名称" min-width="500">
            <template slot-scope="scope">
              <pre>{{scope.row.goods_name}}</pre>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="specification_name" label="规格型号">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="figure_name" label="花纹">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="unit_name" label="单位">
          </el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <el-pagination class="erp-pagination" :current-page.sync="goPage" :page-size="pageSize1" :page-sizes="[30]" @current-change="handleCurrentChange" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel">取 消</el-button>
        <el-button type="primary" @click="submit">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      radio: "",
      gather: [],
      loading: false,
      showGoodsAdd: false, //新增商品弹框
    };
  },
  props: ["isShowGoodsLayer", "companyId"],
  methods: {
    handleSelectionChange(val) {
      this.gather = val;
    },
    handleCurrentChange(val) {
      this.$common.initData("cxzcgoodlist", this.TableData, {
        ...this.params,
        company_id: this.companyId,
        page: val,
      });
    },
    onReset() {
      this.$refs.cascader.clear();
      this.goPage = 1;
      this.$set(this, "params", {
        page: 1,
        company_id: this.companyId,
        type: 2,
      });
      this.$common.initData("cxzcgoodlist", this.TableData, this.params);
    },
    onSearch() {
      this.$common.initData("cxzcgoodlist", this.TableData, this.params);
    },
    cancel() {
      this.$parent.isShowGoodsLayer = false;
    },
    submit() {
      this.$emit("goodsdialog", this.gather);
      this.$parent.isShowGoodsLayer = false;
    },
  },
  mounted() {
    // this.handleItemChange();
    this.$common.initData("cxzcgoodlist", this.TableData, {
      company_id: this.companyId,
      type: 2,
    });
    this.params.company_id = this.companyId;
  },
};
</script>